* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

button {
    border: none;
    font-family: inherit;
}

button:focus {
    outline: none;
}

ul {
    list-style: none;
}

body {
    font-family: 'Istok Web', sans-serif;
}

a {
    text-decoration: none;
}

// end general

body {
    background: #cccccc;
    display: flex;
    height: 100vh;
    justify-content: center;
    flex-direction: column;
}

main {
    width: 960px;
    height: 540px;
    background: white;
    align-self: center;
}